main {
  font-family: "Roboto", sans-serif;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
  padding: 50px 0px;
  padding-bottom: 100px;
}
main div {
  margin-left: 100px;
}
p {
  width: 50%;
  line-height: 1.6;
  font-weight: 400;
  color: black;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}
section {
  background-image: url(../src/person-solid.svg);
  background-repeat: no-repeat;
  width: 100%;
}
footer {
  position: absolute;
  bottom: 10px;
  display: flex;
  flex-direction: row;
  gap: 30px;
  right: 50%;
  transform: translateX(50%);
}
footer svg {
  width: 100%;
  height: 100%;
}
footer a {
  height: 50px;
  width: 50px;
}
footer a:hover svg {
  fill: rgb(206, 206, 206);
}
@media screen and (max-width: 1000px) {
  main {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  section {
    height: 100px;
  }
}
